@font-face {
  font-family: "Maison Neue Bold";
  src: url("../fonts/Font.eot");
  src: url("../fonts/Font.eot?#iefix") format("embedded-opentype"),
    url("../fonts/Font.woff2") format("woff2"),
    url("../fonts/Font.woff") format("woff"),
    url("../fonts/Font.ttf") format("truetype"),
    url("../fonts/Font.svg#Font") format("svg");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Maison Neue";
  src: url("../fonts/Font_1.eot");
  src: url("../fonts/Font_1.eot?#iefix") format("embedded-opentype"),
    url("../fonts/Font_1.woff2") format("woff2"),
    url("../fonts/Font_1.woff") format("woff"),
    url("../fonts/Font_1.ttf") format("truetype"),
    url("../fonts/Font_1.svg#Font") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
